header {
    border-bottom: 1px solid transparent;
    background-color: $alice-blue;
    padding:40px 0;
    @include transition(0.4s);
    * {
        @include transition(0.4s);
    }
    .navbar {
        padding: 0;
    }
    .navbar-brand {
        padding: 0;
        max-width: 262px;
         img{
             width: 100%;
         }
        
    }
    .navigation {
        .menu-main-menu-container {
            margin: 0 40px 0 auto;

            ul {
                padding: 0;
                margin: 0;
                li {
                    display: inline-flex;
                    display: -webkit-inline-flex;
                    flex-direction: column;
                    margin: 0;
                    padding: 0;
                    &::before {
                        display: none;
                    }
                    a {
                        color: $yankee-blue;
                        text-transform: capitalize;
                        font-family: $font-mittelschrift;
                        font-size: 16px;
                        font-weight: 400;
                        line-height: 20px; 
                        text-align: center;
                        &:hover{
                            text-decoration: none;
                        }
                        & >  span{
                            @include trans-anim-none();
                        }
                    }
                }
            }
            &>ul {
                &>li {
                    margin-left: 30px;
                    &>a {
                        padding: 19px 22px 16px 22px;
                        text-align: center; 
                        border: 1px solid transparent;
                        &:hover {
                           border-color: #BDCEDD;
                        }
                        
                    }
                    &.current_page_item,&.current_page_parent,&.current-menu-item{
                        &>a {
                            border-color: #BDCEDD;
                        }
                    }
                }
                
            }
        }
        a.cart-link {
            padding: 0;
            display: inline-block;
            vertical-align: middle;
            color: $black-coral;
            img {
                display: inline-block;
                vertical-align: middle;
                margin-right: 10px;
            }
            span {
                display: inline-block;
                vertical-align: middle;
                color: $black-coral; 
                font-size: 18px; 
                line-height: 20px;
                @include media-breakpoint-down(md){
                    line-height: 28px;
                }
            }
    }
         
        
    }
&.headerFix{
    padding:10px 0;
    .navbar-brand{
        max-width: 200px;
    }
    .navigation{
        .menu-main-menu-container{
            & > ul {
                & > li {
                    & > a{
                        padding: 19px 22px 16px 22px;
                    }
                    .submenu-wrap{
                        top: 72px;
                    }
                }
            }
        }
    }
}

}
// Mobile Header
@include media-breakpoint-down(md) {
    header {
            padding: 15px !important;
            @include trans-anim-none();
            *{
                @include trans-anim-none();
            }
            &.navbar-show{
                background-color: $white-color;
            }
            .navbar-toggler {
                border: none;
                outline: none;
                height: 20px;
                width: 20px;
                cursor: pointer;
                background: url(../img/mobile-menu.svg) no-repeat center;
                .navbar-toggler-icon{
                    display: none;
                }
        }
        .navigation{
            max-width: 100%;
            width: 100%;
            position: fixed;
            top: 70px;
            left: 0;
            right: 0;
            background: $white-color;
            overflow-y: auto;
            border-bottom: 1px solid #E7EEF5;
            -ms-box-shadow: 0 40px 30px 0 rgba(0,0,0,0.15);
            -o-box-shadow: 0 40px 30px 0 rgba(0,0,0,0.15);
            -webkit-box-shadow: 0 40px 30px 0 rgba(0,0,0,0.15);
            -moz-box-shadow: 0 40px 30px 0 rgba(0,0,0,0.15);
            box-shadow: 0 40px 30px 0 rgba(0,0,0,0.15);
            .menu-main-menu-container {
                margin: 0;
                &> { 
                    ul > li{
                        display: flex;
                        width: 100%;
                        margin: 0;
                        align-items: flex-start;
                        justify-content: center;
                        a{
                            display: block;
                            text-align: left;
                            border-top: 1px solid #E7EEF5;
                            width: 100%;
                            padding: 19px 30px !important;
                        }
                    }
                } 
            }
            a.cart-link{
                display: flex;
                padding: 19px 30px !important;
                border-top: 1px solid #E7EEF5;
                text-align: left;
                background-color: $white-color;


            }
        }  
        .navbar{
            position: static;
        } 
        .container{
            max-width: 100%;
        }
        .navbar-brand{
            max-width: 165px !important;
        }
    } 
    .navbar-show {
        .navbar-toggler{
            background: url(../img/mobile-menu-close.svg) no-repeat center;
        }
    }  
} 

@media (max-width: 767.98px) and (orientation: landscape) {
    header .navigation{
        height: calc(100% - 85px);
    }

 }
