// Color Palette //
$black-color: #000000;
$white-color: #ffffff;
$yankee-blue: #172B3B;
$black-coral: #566470;
$alice-blue:#F1F7FC;
$glitter: #E6E8FF;
$medium-gray:#A1A9B0;

// Bootstrap color variable 
$body-bg: $white-color;
$body-color: $black-coral;
$gray-base: #000 !default;
$gray-darker: lighten($gray-base, 13.5%) !default; // #222
$gray-dark: lighten($gray-base, 20%) !default; // #333
$gray: lighten($gray-base, 33.5%) !default; // #555
$gray-light: lighten($gray-base, 46.7%) !default; // #777
$gray-lighter: lighten($gray-base, 93.5%) !default; // #eee
$theme-colors: (
  "primary": $yankee-blue,
  "secondary": $glitter  
);

// link color 
$link-color: $black-coral !default;
$link-hover-color:$yankee-blue !default;
$link-hover-decoration: underline !default;

// Container
$container-max-widths: ( sm: 540px, md: 720px, lg: 960px, xl: 1200px) !default;

// Buttons 
$btn-font-weight: normal !default;
$btn-border-radius: 0px !default;

// Forms Input 
$input-bg: #f9f8f2 !default;
$input-bg-disabled: $gray-lighter !default;
$input-color: $body-color !default;
$input-border-width: 1px;
$input-border-color: $medium-gray !default;
$input-border-radius: 0px !default;
$input-font-size: 14px !default;
$input-font-weight: 700 !default;
$input-focus-border-color: $yankee-blue !default;
$input-focus-color: $body-color !default;
$input-placeholder-color: $medium-gray !default;
$input-height: 50px !default;
$input-focus-box-shadow: none !default;

// form-group margin 
$form-group-margin-bottom: 15px !default;
