.cmn-banner {
  min-height: 350px;
  padding: 190px 0 95px 0;
  background-color: #f1f7fc;
  -o-box-shadow: inset 0 -40px 30px 0 rgba(0, 0, 0, 0.05);
  -webkit-box-shadow: inset 0 -40px 30px 0 rgba(0, 0, 0, 0.05);
  -moz-box-shadow: inset 0 -40px 30px 0 rgba(0, 0, 0, 0.05);
  box-shadow: inset 0 -40px 30px 0 rgba(0, 0, 0, 0.05);
  &.no-box-shadow{
    box-shadow: none;
  }
  @include media-breakpoint-down(md) {
    padding: 130px 0 80px 0;
    min-height: 285px;
  }
  h1 {
    margin-bottom: 40px;
  }
  .hero-image {
    margin-bottom: -220px;
    @include media-breakpoint-down(sm) {
      margin-bottom: 45px;
    }
    img {
      max-height: 720px;
      @include media-breakpoint-down(sm) {
        max-height: 360px;
      }
    }
  }
  .btn-primary {
    text-transform: inherit;
  }
}

.zigzag-section {
  padding: 25px 0;
  @include media-breakpoint-down(sm) {
    padding: 0 0 20px;
  }
  .zigzag-box {
    padding: 75px 0;
    background-color: $white-color;
    @include media-breakpoint-down(sm) {
      padding: 90px 0;

      &:nth-child(1) {
        img {
          margin-top: 74px;
        }
      }
      &:nth-child(2) {
        img {
          margin-bottom: 60px;
        }
      }
      &:nth-child(3) {
        img {
          margin-top: 60px;
        }
      }
    }
    &:nth-child(even) {
      background-color: $alice-blue;
    }
    img {
      height: auto;
    }
    .content-box {
      text-align: center;
      h3 {
        margin-bottom: 18px;
      }
      p:last-child {
        margin-bottom: 0;
      }
      img {
        margin: 0 0 30px;
        height: auto;
        max-width: 54px;
      }
    }
  }
}

// Testimonials Slider
.cmn-testimonials{
  padding: 150px 0 75px 0;
  @include media-breakpoint-down(lg) {
      padding: 100px 0 75px 0;
  }
  
  text-align: center;
  h2{
      margin-bottom: 105px;
  }
}
.testimonials-slider{
  .slick-list{
      overflow: visible;
  }
  .item{
      width: 570px;
      background-color: $white-color;
      -ms-box-shadow: 0 20px 30px 0 rgba(0,0,0,0.1);
      -o-box-shadow: 0 20px 30px 0 rgba(0,0,0,0.1);
      -webkit-box-shadow: 0 20px 30px 0 rgba(0,0,0,0.1);
      -moz-box-shadow: 0 20px 30px 0 rgba(0,0,0,0.1);
      box-shadow: 0 20px 30px 0 rgba(0,0,0,0.1);
      margin: 0 15px;
      padding: 70px 100px 97px 100px;
      text-align: center;
      @include opacity(0.25);
      @include transition(0.4s);
      position: relative;
      &.slick-active.slick-center{
          @include opacity(1);
      }
      @include media-breakpoint-down(md) {
          padding: 70px 40px 78px 40px;
      }
      @include media-breakpoint-down(sm) {
        width: 400px;
     }
    @include media-breakpoint-down(xs) {
        width: 316px;
    }
  }
  .avatar-wrap {
      display: block;
      width: 70px;
      height: 70px;
      @include border-radius(50%);
      overflow: hidden;
      margin:-105px auto 0 ;
  }
  p{
      margin: 35px 0 25px 0;
  }
  h4{
      margin-bottom: 0;
      position: absolute;
      bottom: 70px;
      left: 0;
      right: 0;
      @include media-breakpoint-down(md) {
          bottom: 55px;
      }
  }
}

.bobcat-carpet{
  padding: 75px 0 150px 0;
  position: relative;
  @include media-breakpoint-down(md) {
    padding: 75px 0;
  }
  @include media-breakpoint-down(sm) {
    padding-top:40px;  
  }
  .bobcat-left{
    .bobcat-logo{
      display: block;
    }
    .h1{
      margin: 50px 0;
      @include media-breakpoint-down(sm) {
        margin: 45px 0 25px 0;
      }
    }
    
  }
  .btn{
    min-width: 235px;
    text-transform: inherit;
  }
  .bobcat-right{
    position: relative;
    align-self: center;
    img{
      float: left;
      height: auto;
      max-width: none;
      margin-left: -15px;
      @include media-breakpoint-down(lg) {
        max-width: 920px;
      }
      @include media-breakpoint-down(md) {
        max-width: 1040px;
      }
      @include media-breakpoint-down(sm) {
        margin: 50px 0px 50px -15px;
        max-width: 768px;
      }
      @include media-breakpoint-down(xs) {
        max-width: 550px;
      }
    }
  }
}

// After and before slider section
.stain-be {
  background-color: #F1F7FC;
  padding-bottom: 0;
  padding-top: 75px;
  @include media-breakpoint-down(sm) {
    padding-top:45px; 
  }
  .stain-banner {
    width: 100%;
    position: relative;
    max-width: 1440px;
    margin: 0 auto;
  }
  .beer-slider{
    float: left;
    width: 100%;
    height: 600px;
    @include media-breakpoint-down(lg) {
      height: 450px;
    }
    @include media-breakpoint-down(sm) {
      height: 300px;
    } 
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
  }
  h2{
    margin-bottom: 10px;
  }
  p{
    margin-bottom: 35px;
  }
}
@media screen and (max-width:480px){
  .bobcat-carpet .bobcat-right img{
    max-width: 520px;margin: 55px 0px 40px -20px;
  }
}
