.shopping-cart {
  padding: 70px 0 90px;
  min-height: 600px;
  @include media-breakpoint-down(md) {
    padding: 50px 0;
    min-height: 1px;
  }
}
.woocommerce-cart-form {
  margin-top: -15px;
  @include media-breakpoint-down(md) {
    margin-top: -36px;
  }
  .cart_item {
    border-bottom: 1px;
    padding-bottom: 100px;
    margin-top: 66px;
    border-bottom: 1px solid #e7eef5;
    @include media-breakpoint-down(md) {
      margin-top: 36px;
      padding-bottom: 40px;
    }
    .product-thumbnail {
      text-align: center;
      a {
        height: 118px;
        width: 70px;
        display: block;
        text-align: center;
        /*======Only IE 11 css=====*/
        @media screen and (-ms-high-contrast: active),
          (-ms-high-contrast: none) {
          height: auto;
          img {
            height: auto;
            width: auto;
            -o-object-fit: none;
            object-fit: none;
            max-width: 60px;
          }
        }

        img {
          height: 100%;
          width: 100%;
          -o-object-fit: contain;
          object-fit: contain;
        }
      }
      @include media-breakpoint-down(md) {
        text-align: left;
        margin-bottom: 20px;
      }
    }
    .product-name {
      br {
        display: none;
      }
      .h4 {
        display: block;
        margin-bottom: 0;
        sup {
          top: 1px;
          font-size: 20px;
          line-height: 28px;
        }
        @include media-breakpoint-up(lg) {
          margin-left: -30px;
          max-width: 176px;
        }
        &:hover {
          text-decoration: none !important;
        }
        small {
          @include font-sm;
        }
      }
      .product-sku {
        font-size: 14px;
        line-height: 20px;
        font-family: $font-gothic;
        text-transform: uppercase;
        display: block;
        margin-top: 8px;
        @include media-breakpoint-up(lg) {
          margin-left: -30px;
        }
        @include media-breakpoint-down(sm) {
          margin-bottom: 8px;
        }
      }
    }
    .product-subtotal {
      padding-top: 15px;
    }
  }
}

.woocommerce .cart-collaterals .cart_totals,
.woocommerce-page .cart-collaterals .cart_totals {
  float: none;
  width: 100%;
}

.quantity {
  border: 1px solid #a1a9b0;
  display: inline-block;
  vertical-align: top;

  input {
    display: inline-block;
    vertical-align: top;
    height: 58px;
    line-height: 58px;
    outline: none;
    &[type="button"] {
      background: #fff;
      border: none;
      width: 51px;
      display: inline-block;
      padding: 0;
      font-size: 0;

      &.minus {
        background: url(../img/minus.svg) no-repeat center;
      }
      &.plus {
        background: url(../img/plus.svg) no-repeat center;
      }
    }
    &[type="number"] {
      border: none;
      color: $yankee-blue;
      background: transparent;
    }
    @include media-breakpoint-down(sm) {
      width: 50px !important;
    }
  }
}

.woocommerce a.remove {
  margin: 15px 0 0;
  height: auto;
  color: $yankee-blue !important;
  font-size: 18px;
  line-height: 22px;
  text-decoration: underline;
  font-weight: 400;
  &:hover {
    color: $glitter !important;
    background: transparent;
  }
}
.amount {
  color: $yankee-blue !important;
  @include font-sm;
}

.sub-total {
  padding: 32px 0;
  @include media-breakpoint-down(lg) {
    > div:first-child {
      margin-bottom: 10px;
    }
  }
  .amount {
    @include font-md;
    margin-left: 115px;
    @include media-breakpoint-down(lg) {
      margin-left: 20px;
    }
  }
  a {
    text-decoration: underline;
    color: $yankee-blue;
    &:hover {
      color: $glitter;
    }
  }
  + table {
    display: none;
  }
}

.product-summary {
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 1px;
    height: 100%;
    background: #e7eef5;
    @include media-breakpoint-up(xl) {
      left: -15px;
    }
    @include media-breakpoint-down(md) {
      display: none;
    }
  }

  .cart-collaterals {
    .checkout-button {
      width: 100%;
    }
    table {
      border: none;
      tr {
        th {
          font-size: 14px;
          line-height: 20px;
          color: $black-coral;
          font-family: $font-gothic;
          font-weight: 400 !important;
          width: auto !important;
        }
        td {
          font-size: 18px;
          line-height: 34px;
          font-family: $font-body;
          text-align: right;
          background: none !important;
        }
        &.order-total {
          td {
            border: none !important;
            padding-top: 0 !important;
            .amount {
              @include font-lg;
              line-height: 1.18;
            }

            @include media-breakpoint-down(md) {
              margin-top: 20px;
            }
          }
        }
        &.shipping {
          td {
            font-size: 14px;
            line-height: 20px;
            color: $black-coral;
            font-family: $font-gothic;
            font-weight: 400 !important;
            strong {
              font-size: 14px;
              line-height: 20px;
              color: $black-coral;
              font-family: $font-gothic;
              font-weight: 400 !important;
              text-transform: lowercase;
            }
          }
        }
      }
    }
  }
}

.woocommerce-checkout-review-order-table,
.order_details {
  tr {
    th,
    td {
      font-size: 14px;
      line-height: 20px;
      color: $black-coral;
      font-family: $font-gothic;
      font-weight: 400 !important;
      sup {
        font-size: 14px;
        line-height: 20px;
        color: $black-coral;
        font-family: $font-gothic;
        font-weight: 400 !important;
        top: 1px;
      }
    }
  }
}

.woocommerce-checkout {
  h3 {
    label {
      margin: 0;
    }
  }
  p {
    display: block;
  }
  input {
    &[type="text"],
    &[type="email"],
    &[type="tel"] {
      padding: 17px 20px;
      border: 1px solid #a1a9b0 !important;
      color: $black-coral;
    }
  }
  textarea,
  .select2-selection,
  .payment_box input,
  #billing_country_field .woocommerce-input-wrapper strong {
    padding: 17px 20px !important;
    border: 1px solid #a1a9b0 !important;
    font-size: 18px !important;
    line-height: 18px;
    width: 100%;
    display: block;
    color: #000;
    font-weight: 400;
    color: $black-coral;
  }
  .select2-selection {
    margin-bottom: 6px;
  }
  .select2-search__field {
    padding: 4px !important;
  }
}
.woocommerce-shipping-methods {
  margin: 0;
  padding: 0;
  li {
    padding: 0;
    &:before {
      display: none;
    }
  }
}
.woocommerce-thankyou-order-details {
  margin-bottom: 20px !important;
  li {
    margin-bottom: 2rem;
    &:before {
      display: none;
    }
    strong {
      margin-top: 5px;
      color: $yankee-blue;
      font-family: $font-gothic;
    }
    @include media-breakpoint-down(md) {
      margin-right: 1em !important;
    }
    @include media-breakpoint-down(md) {
      display: block;
      border: none !important;
      border-bottom: 1px dashed #d3ced2 !important;
      margin: 0 !important;
      padding: 15px 0 10px !important;
      width: 100%;
    }
  }
}
.woocommerce-order-details {
  padding: 0;
}
.order-total-text {
  td {
    color: $black-coral;
    font-size: 18px;
    line-height: 34px;
    text-align: right;
    text-transform: capitalize;
    font-family: $font-body;
    padding-top: 25px !important;
    padding-bottom: 0 !important;
  }

  @media (max-width: 768px) {
    display: none !important;
  }
}

.cart_totals table.shop_table td,
.cart_totals table.shop_table th {
  padding: 20px 0 9px !important;
}
.order-rec .amount,
.cart_totals .amount,
.woocommerce-checkout-review-order .amount {
  font-size: 18px;
  font-weight: 400;
  font-family: $font-body;
  line-height: 24px;
}
.woocommerce ul#shipping_method .amount {
  font-weight: 400;
}

.order-rec {
  h2 {
    @include font-md;
  }
  thead {
    .woocommerce-table__product-name,
    .woocommerce-table__product-table {
      font-weight: 700 !important;
    }
  }
  td {
    min-width: 100px;
    .shipped_via {
      display: block;
    }
  }
  .woocommerce-customer-details {
    padding: 0;
    address {
      word-break: break-word;
    }
  }
}
.order-brief {
  li {
    @include media-breakpoint-down(lg) {
      margin-right: 1em !important;
      padding-right: 1em !important;
    }
    @include media-breakpoint-down(md) {
      margin-right: 0 !important;
      padding-right: 0 !important;
    }
  }
  .amount {
    font-weight: 700 !important;
    font-size: 18px;
  }
}
.woocommerce-shipping-destination {
  display: none;
}
.restore-item,
.woocommerce-error .wc-backward {
  color: #fff;
  &:hover {
    color: #fff;
  }
}

.woocommerce-checkout {
  h3 {
    margin: 30px 0;
  }
}
.woocommerce-NoticeGroup-checkout {
  width: 100%;
  li:before {
    display: none;
  }
}
.woocommerce table.shop_table_responsive tr td::before,
.woocommerce-page table.shop_table_responsive tr td::before {
  @include media-breakpoint-down(md) {
    font-size: 14px;
    line-height: 20px;
    color: #566470;
    font-family: "Gothic A1", sans-serif;
    font-weight: 400 !important;
    width: auto !important;
  }
}
