.contact-banner {
  box-shadow: none;
  @include media-breakpoint-down(md) {
    padding: 150px 0 75px 0;
  }
}
.contact-section {
  padding: 95px 0;
  @include media-breakpoint-down(md) {
    padding: 70px 0;
  }
  p {
    margin-bottom: 50px;
  }
  .contact-img {
    img {
      margin-top: -280px;
      width: 470px;
      height: auto;
      @include media-breakpoint-down(md) {
        margin: 20px 0 0;
      }
    }
  }
  h3 {
    margin-bottom: 15px;
  }
  strong {
    @include font-sm;
    display: block;
    margin: 5px 0 0;
    a {
      color: $yankee-blue;
      &:hover {
        text-decoration: none;
        color: $glitter;
      }
    }
  }
}