// Font Family
$font-body: "Libre Baskerville", serif;
$font-gothic: "Gothic A1", sans-serif;
$font-mittelschrift: "Mittelschrift";

// Fluid Fonts
$min_width: 375px;
$max_width: 1200px; 
$min_font: 18px;
$max_font: 18px;
@mixin body-font {
  font-family: $font-body;
  font-weight: 400;
  line-height: 1.9;
  color: $black-coral;
  @include fluid-type($min_width, $max_width, $min_font, $max_font);
}

// Font-size as h1-h6 headings
@mixin font-xl {
  color: $yankee-blue;
  font-family: $font-mittelschrift;
  font-weight: 400;
  letter-spacing: -0.5px;
  line-height: 1.23;
  @include fluid-type($min_width, $max_width, 38px, 52px);
  margin-bottom: 20px;
  @include media-breakpoint-down(sm) {
    letter-spacing: normal;
  }
}

@mixin font-lg {
  color: $yankee-blue;
  font-family: $font-mittelschrift;
  font-weight: 400;
  line-height: 1.17;
  @include fluid-type($min_width, $max_width, 28px, 36px);
  margin-bottom: 40px;
}

@mixin font-md {
  color: $yankee-blue;
  font-family: $font-mittelschrift;
  font-weight: 400;
  line-height: 1.34;
  @include fluid-type($min_width, $max_width, 22px, 24px);
  margin-bottom: 25px;
}

@mixin font-sm {
  color: $yankee-blue;
  font-family: $font-mittelschrift;
  font-weight: 400;
  line-height: 1.4;
  @include fluid-type($min_width, $max_width, 20px, 20px);
  margin-bottom: 25px;
}

@mixin font-xs {
  color: $yankee-blue;
  font-family: $font-mittelschrift;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  margin-bottom: 25px;
}

@mixin font-xxs {
  color: $yankee-blue;
  font-family: $font-mittelschrift;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  margin-bottom: 25px;
}

@mixin border-radius($radius) {
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
  -o-border-radius: $radius;
  border-radius: $radius;
}

@mixin opacity($opacity) {
  -webkit-opacity: $opacity;
  -moz-opacity: $opacity;
  -o-opacity: $opacity;
  -m-opacity: $opacity;
  opacity: $opacity;
}
@mixin transition($seconds) {
  -webkit-transition: all $seconds ease;
  -moz-transition: all $seconds ease;
  -ms-transition: all $seconds ease;
  -o-transition: all $seconds ease;
  transition: all $seconds ease;
}
@mixin trans-anim-none {
  -webkit-transition: none;
  -moz-transition: none;
  -o-transition: none;
  transition: none;
}

@mixin space-none {
  margin: 0;
  padding: 0;
}

@mixin app-none {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  -ms-appearance: none;
  appearance: none;
}

@mixin point-none {
  -webkit-pointer-events: none;
  -moz-pointer-events: none;
  -o-pointer-events: none;
  -ms-pointer-events: none;
  pointer-events: none;
}

// Background properties
@mixin bg-cmn {
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}

@mixin bg-parallax {
  background-attachment: fixed;
  @include bg-cmn;
}