.about-banner {
  box-shadow: none;
  @include media-breakpoint-down(md) {
    padding-bottom: 65px;
  }
  img {
    height: auto;
  }
}
.about-section {
  padding: 115px 0 110px;
  @include media-breakpoint-down(md){
    padding: 70px 0;
  }
  img {
    height: auto;    
    max-width: none;
    margin-top: -30px;
    width: 100%;
    @include media-breakpoint-down(md){
      margin: 0;
      max-width: 100%;
    }
  }
}
.about-img {
  padding: 0;
  @include media-breakpoint-down(md){
    height: 360px;
  } 
  @include media-breakpoint-down(sm){
    height: 300px; 
  }
  img {
    width: 100%;
    height: auto;
    @include media-breakpoint-down(md){
      -o-object-fit: cover;
      object-fit: cover;
      height: 100%;
    }
  }
}