.faq-content{
    padding: 90px 0px 125px;
    @include media-breakpoint-down(lg){
        padding: 60px 0px 30px;
    }
    ul.list-unstyled{
      margin-right: -15px;
      margin-left: -15px;
      @include media-breakpoint-down(md){
        height: auto !important;
      }
      & > li{
        padding-left: 15px;
        padding-right: 15px;
        margin-bottom: 0px;
        @include media-breakpoint-up(lg){
            width: 41.66667%;
            float: left;      
            margin-right: 8.33333% !important;
            margin-bottom: 27px;
        }
        @include media-breakpoint-down(md){
          position: static !important;
        }
        ul{
          float: left;
          width: 100%;
          margin-bottom: 50px;
          li{
            padding: 0px 0px 0px 25px;
            &:before { 
              content:".";
            }
          }
        } 
        & > ol{
          float: left;
          width: 100%;
          margin-bottom: 23px;
          & >  li{
            padding: 0px 0px 0px 25px;
            &:before { 
              content: counter(av-counter) ".";
              top: 10px;
              font-size: 18px;
              color: #566470;
            }
            ol{
              margin: 30px 0px 30px;
              padding: 0px;
              list-style: none;
              li{
                counter-increment: av-counter2;
                  @include body-font;
                  margin: 0px 0px 5px;
                  padding: 0px 0px 0px 20px;
                  position: relative;
                  &:before {
                    content: counter(av-counter2) ".";
                    counter-increment: none;
                    color: $black-color;
                    position: absolute;
                    left: 0px;
                    top: 10px;
                    font-size: 18px;
                    color: #566470;
                  }
              }
             }
          }
        }
      } 
    }  
    h3{
      margin-bottom: 15px;
    }  
  }