.buy-now {
    background: $yankee-blue;
    color: $white-color;
    display: block;
    font-family: $font-gothic;
    overflow: hidden;
    padding: 78px 0px;
    text-decoration: none;
    width: 100%;

    .container {
        align-items: center;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
    }

    .h2 {
        display: inline-block;
        margin: 0px 20px 0px 0px;
        color: $white-color; 
    }
    svg {
        height: 23px;
        width: 50px;
    }

    &:hover {
        background: $glitter;
        color: $yankee-blue;
        text-decoration: none;
        svg path {
            fill: $yankee-blue !important;
        }
        .h2{
            color: $yankee-blue;
        }
    }
}

footer {
    font-family: $font-gothic;
    float: left;
    font-size: 14px;
    line-height: 20px;
    padding: 58px 0px 25px;
    width: 100%; 
    @include media-breakpoint-down(sm) {
        text-align: center;
    }
    @include media-breakpoint-down(sm) {
        padding: 75px 0px 35px;
    }

    .f-logo {
        @include media-breakpoint-down(sm) {
            margin-bottom: 58px;
        }
        a {
            display: inline-block;
            &:hover {
                @include opacity(0.8);
            }
        }
    }

    .menu-footer-menu-container {
        float: right;
        @include media-breakpoint-down(sm) {
            width: 100%;
        }
        .menu {
            margin: 0px;
            overflow: hidden;

            li {
                float: left;
                margin: 0px 0px 0px 70px;
                padding: 0px;

                @include media-breakpoint-down(md) {
                    margin: 0px 0px 0px 28px;
                }

                @include media-breakpoint-down(sm) {
                    margin: 0px 0px 20px;
                    width: 100%;
                }

                &:before {
                    content: normal;
                }

                &:first-child {
                    margin-left: 0px;
                }

                a {
                    color: $yankee-blue;
                    display: block;
                    font-family: $font-mittelschrift;
                    font-size: 16px;
                    font-weight: 400;
                    line-height: 20px;
                    padding: 2px;
                    text-decoration: none;

                    @include media-breakpoint-down(sm) { 
                        display: inline-block;
                        padding: 2px 10px;
                    }
                    
                    &:before,
                    &:after {
                        background: #BDCEDD;
                        border-radius: 0px;
                        content: "";
                        position: absolute;
                        top: auto;
                        bottom: 0px;
                        height: 1px;
                        width: 0;
                        @include media-breakpoint-down(sm) {
                            display: none;
                        }
                    }

                    &:before {
                        left: 0px;
                    }
                    &:after {
                        right: 0;
                        -o-transition: width 0.85s cubic-bezier(0.22, 0.61, 0.36, 1);
                        -webkit-transition: width 0.85s cubic-bezier(0.22, 0.61, 0.36, 1);
                        -moz-transition: width 0.85s cubic-bezier(0.22, 0.61, 0.36, 1);
                        transition: width 0.85s cubic-bezier(0.22, 0.61, 0.36, 1);
                    }

                    &:hover {
                        text-decoration: none;

                        &:before {
                            width: 100%;
                            -o-transition: width 0.55s cubic-bezier(0.22, 0.61, 0.36, 1);
                            -webkit-transition: width 0.55s cubic-bezier(0.22, 0.61, 0.36, 1);
                            -moz-transition: width 0.55s cubic-bezier(0.22, 0.61, 0.36, 1);
                            transition: width 0.55s cubic-bezier(0.22, 0.61, 0.36, 1);
                        }
                        &:after {
                            width: 100%;
                            transition: 0s;
                            background: 0;
                        }
                    }
                }
            }
        }
    }

    .copy-txt {
        margin-top: 58px;
        @include media-breakpoint-down(sm) { 
            margin-top: 38px;
            span {
                display: block;
            }
        }
    }
}

