.pr-detail-banner {
  box-shadow: none;
  .hero-image {
    @include media-breakpoint-down(md) {
      margin-bottom: 0px;
    }
  }
  h1 {
    margin-bottom: 16px;
    letter-spacing: 0px;
    sup{
        font-size: 52px;
      letter-spacing: -0.5px;
      line-height: 64px;
      top:1px;
    }
  }
  h5 {
    color: $black-coral;
    font-family: $font-gothic;
    margin-bottom: 25px;
    text-transform: uppercase;
  }
  .woocommerce-message {
    display: none;
  }
}
.pr-feature {
  padding: 180px 0px 45px;
  text-align: center;
  @include media-breakpoint-down(md) {
    padding: 80px 0px 40px;
  }
  .feature-icn {
    display: block;
    margin: 0px auto 15px;
    line-height: 70px;
    height: 70px;
    width: 70px;
    img {
      display: inline-block;
      max-height: 60px;
    }
  }
}
.pr-detail-testi {
  padding: 75px 0px 150px;
  @include media-breakpoint-down(md) {
    padding: 75px 0px 100px;
  }
}
h1.product_title {
  small {
    font-weight: bold;
    font-size: 17px;
    vertical-align: middle;
  }
}
.woocommerce {
  .variations {
    margin-bottom: 25px !important;
    td.label {
      display: none;
    }
    .reset_variations {
      display: none !important;
    }
    .tawcvs-swatches {
      @include media-breakpoint-down(sm){
        padding: 5px 0;
      }
      .swatch {
        width: auto;
        height: auto;
        font-size: 18px;
        line-height: 34px;
        border-radius: 0;
        box-shadow: none !important;
        border: solid transparent !important;
        border-width: 0px 0px 4px 0px !important;
        margin: 0px;
        padding: 0px 15px;
        color: #566470;
        background: transparent;
        opacity: 1;
        &.selected {
          color: $yankee-blue;
          border-color: $yankee-blue !important;
        }
        @media (max-width:375px){
          padding: 0px 10px;
          font-size: 16px;
        }
      }
    }
  }
  .product {
    .single_variation {
      margin-bottom: 26px;
      .price .amount {
        @extend h3;
        color: $yankee-blue !important;
        font-size: 24px !important;
      }
    }
    .images {
      width: 100% !important;
      margin-top: 10px;
      img {
        width: auto !important;
        display: inline-block !important;
        max-height: 650px;
      }
    }
    .cart {
      margin-bottom: 0px !important;
      .quantity {
        margin-right: 10px !important;
        @include media-breakpoint-down(xs) {
          margin-bottom: 15px !important;
        }
      }
    }
  }
  .woocommerce-product-details__short-description {
    padding-bottom: 10px;
    p {
      margin-bottom: 15px;
    }
  }
}
.woocommerce button.button.alt,
.woocommerce a.button.alt {
  @extend .btn;
  @extend .btn-primary;
  &:hover,
  &:active,
  &:focus {
    box-shadow: 0 0 40px 0 #566470 !important;
  }
}
.single-product {
  .cmn-testimonials {
    padding: 70px 0 150px 0;
  }
}
.woocommerce-variation-add-to-cart-disabled button.button.alt.disabled {
  color: #fff;
  background-color: #0c171f !important;
  border-color: #091016 !important;
  opacity: 1 !important;
}
.woocommerce .woocommerce-message,
.woocommerce .woocommerce-info,
.woocommerce-error {
  background: #172b3b;
  color: #fff;
  border: none;
  > li:last-child {
    margin-bottom: 0;
  }
  .button {
    @extend .btn;
    @extend .btn-secondary;
    padding: 10px 15px 7px;
    line-height: 1;
    border-radius: 0px;
  }
}