@include media-breakpoint-down(xs) {
  .container {
    width: calc(100% - 30px);
  }
}
body {
  @include body-font;
}
.page-wrapper {
  display: block;
  overflow: hidden;
  width: 100%;
}
main {
  float: left;
  min-height: 600px;
  padding: 0px 0px;
  width: 100%;
  @include media-breakpoint-down(md) {
    min-height: 500px;
  }
  @include media-breakpoint-down(sm) {
    min-height: 400px;
  }
}

section {
  float: left;
  padding: 70px 0px;
  width: 100%;
}

img {
  display: inline-block;
  max-width: 100%;
}

.svg {
  pointer-events: none;
}

.div-img {
  @include bg-cmn;
}

// Heading Style
h1,
.h1 {
  @include font-xl;
}

h2,
.h2 {
  @include font-lg;
}

h3,
.h3 {
  @include font-md;
}

h4,
.h4 {
  @include font-sm;
}

h5,
.h5 {
  @include font-xs;
}

h6,
.h6 {
  @include font-xxs;
}

// Listing Style
ul {
  margin: 0px 0px 5px;
  padding: 0;
  li {
    list-style: none;
    margin: 0px 0px 10px;
    padding: 0px 0px 0px 15px;
    position: relative;
    &:before {
      content: ".";
      position: absolute;
      left: 0px;
      top: 0;
      font-size: 35px;
      color: $yankee-blue;
      line-height: 12px;
    }
  }
}

ul.list-unstyled {
  li {
    margin: 0px;
    padding: 0px;
    &:before {
      content: normal;
    }
  }
}

ol {
  margin: 0px 0px 30px;
  padding: 0px;
  list-style: none;
  li {
    counter-increment: av-counter;
    @include body-font;
    margin: 0px 0px 20px;
    padding: 0px 0px 0px 20px;
    position: relative;
    &:before {
      content: counter(av-counter) ".";
      counter-increment: none;
      color: $black-color;
      position: absolute;
      left: 0px;
      top: 0px;
    }
  }
}

a {
  text-decoration: none;
}

p {
  margin-bottom: 40px;
}

// All buttons
.btn {
  border: none;
  font-family: $font-mittelschrift;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
  padding: 19px 22px;
  @include border-radius(2px);
  @include transition(0.2s);

  &.btn-primary {
    background-color: $yankee-blue;
    color: $white-color;
    &:hover,
    &:focus,
    &:active,
    &:not(:disabled):not(.disabled):active {
      background-color: $yankee-blue;
      color: $white-color;
      -ms-box-shadow: 0 0 40px 0 #566470;
      -o-box-shadow: 0 0 40px 0 #566470;
      -webkit-box-shadow: 0 0 40px 0 #566470;
      -moz-box-shadow: 0 0 40px 0 #566470;
      box-shadow: 0 0 40px 0 #566470;
    }
  }
  &.btn-secondary {
    background-color: $glitter;
    color: $yankee-blue;
    &:hover,
    &:focus,
    &:active,
    &:not(:disabled):not(.disabled):active {
      background-color: $glitter;
      color: $yankee-blue;
      -ms-box-shadow: 0 0 40px 0 rgba(195, 201, 252, 0.5);
      -o-box-shadow: 0 0 40px 0 rgba(195, 201, 252, 0.5);
      -webkit-box-shadow: 0 0 40px 0 rgba(195, 201, 252, 0.5);
      -moz-box-shadow: 0 0 40px 0 rgba(195, 201, 252, 0.5);
      box-shadow: 0 0 40px 0 rgba(195, 201, 252, 0.5);
      color: $yankee-blue;
    }
  }
  &:hover,
  &:focus {
    box-shadow: none;
  }
}

// Form Controls
.form-group {
  margin-bottom: 30px;
  label {
    .required {
      color: $yankee-blue;
    }
  }
}

.form-control {
  border-color: $medium-gray;
  color: $black-coral;
  font-family: $font-body;
  font-size: 18px;
  line-height: 24px;
  padding: 17px 20px;
  height: auto;
  font-weight: normal;
  @include border-radius(0);
  background-color: $white-color;
  &:focus {
    background-color: $white-color;
  }
}

.textarea {
  label {
    color: $black-color;
  }
}

textarea {
  &.form-control {
    height: 150px;
    resize: none;
  }
}

textarea + label {
  color: $black-color;
}

.form-label {
  display: block;
  position: relative;
}

// Select box Style
.selection {
  display: block;
}
.select2-container {
  z-index: 99;
  max-width: 100%;
  @include media-breakpoint-down(lg) {
    max-width: 210px;
  }
  @include media-breakpoint-down(md) {
    max-width: 100%;
  }
}
.select2-container--default .select2-selection--single {
  border: 1px solid #d1d7db;
  @include border-radius(0);
  color: $black-coral;
  font-family: $font-body;
  font-size: 18px;
  line-height: 24px;
  outline: none;
  padding: 17px 20px;
  height: auto;
  letter-spacing: normal;
}
.select2-container .select2-selection--single .select2-selection__rendered {
  padding: 0;
  outline: none;
  color: $black-coral;
  font-family: $font-body;
  font-size: 18px;
  line-height: 24px;
}
.select2-container--default
  .select2-selection--single
  .select2-selection__arrow {
  margin: auto;
  top: 14px;
  right: 5px;
  background: url("../img/Dropdown-arrow.svg") no-repeat center center;
  height: 36px;
  width: 36px;
}
.select2-container--default
  .select2-selection--single
  .select2-selection__arrow
  b {
  display: none;
}
.select2-container--open.select2-container--default
  .select2-selection--single
  .select2-selection__arrow {
  -o-transform: rotate(-180deg);
  -webkit-transform: rotate(-180deg);
  -moz-transform: rotate(-180deg);
  transform: rotate(-180deg);
}
.select2-container--open.select2-container--default .select2-selection--single {
  color: $black-color;
  border: 1px solid #cbd9db;
}
.select2-container--default.select2-container--open.select2-container--below
  .select2-selection--single,
.select2-container--default.select2-container--open.select2-container--below
  .select2-selection--multiple {
  border-color: $yankee-blue !important;
}
.select2-dropdown,
.select2-container--open .select2-dropdown--below {
  border: 1px solid #dfdfdf;
  background-color: #ffffff;
  -o-box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.15);
  -webkit-box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.15);
  -moz-box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.15);
  box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.15);
  @include border-radius(0);
}
.select2-dropdown ul li:before {
  content: normal;
}
.select2-dropdown .mCSB_inside > .mCSB_container {
  margin-right: 0px;
}
.select2-results__option {
  margin: 0px;
  padding: 10px 19px;
  color: $black-color;
  font-family: $font-body;
  font-size: 16px;
  line-height: 28px;
  background: #ffffff;
  border-bottom: 1px solid #dfdfdf;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  &:first-child {
    border-top: 1px solid #dfdfdf;
    &:hover {
      border-top-color: transparent;
    }
  }
}
.select2-container--default .select2-results__option[aria-selected="true"] {
  margin: 0px;
  padding: 10px 19px;
  color: $black-color;
  font-family: $font-body;
  font-size: 16px;
  line-height: 28px;
  background: #ffffff;
}
.select2-container--default
  .select2-results__option--highlighted[aria-selected] {
  color: $white-color;
  background-color: $yankee-blue;
  border-top-color: transparent;
}
.select2-container--default .select2-results__option[aria-disabled="true"] {
  display: none;
  background-color: #ffffff;
  color: white;
}
.select2-container--default.select2-container--disabled
  .select2-selection--single {
  cursor: not-allowed;
}
.select2-container--default .select2-results > .select2-results__options {
  margin-left: -1px;
  margin-top: -1px;
  margin-right: -1px;
}

// M Custom Scrollbar
.mCSB_inside > .mCSB_container {
  margin-right: 16px;
}
.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar,
.mCSB_scrollTools .mCSB_dragger.mCSB_dragger_onDrag .mCSB_dragger_bar,
.mCSB_scrollTools .mCSB_dragger:active .mCSB_dragger_bar,
.mCSB_scrollTools .mCSB_dragger:hover .mCSB_dragger_bar {
  background-color: $yankee-blue;
}
.mCSB_scrollTools .mCSB_draggerRail {
  background: $yankee-blue;
}
.mCustomScrollBox > .mCSB_scrollTools {
  top: 20px;
  bottom: 20px;
  height: auto;
}
.mCSB_scrollTools .mCSB_draggerRail {
  background-color: rgba(0, 0, 0, 0.2);
}

// Contact form Validation
div.wpcf7-response-output {
  border: none;
  font-weight: 700;
  line-height: 22px;
  margin: 10px 0px 20px;
  padding: 0px;
  text-align: center;
}

.wpcf7-form-control-wrap {
  display: block;
  position: relative;
}

div.wpcf7-validation-errors,
div.wpcf7-acceptance-missing {
  color: #a59b0e;
}

div.wpcf7-mail-sent-ng,
div.wpcf7-aborted {
  color: #ff0000;
}

div.wpcf7-mail-sent-ok {
  color: #398f14;
}

span.wpcf7-not-valid-tip {
  margin-top: 5px;
  font-size: 12px;
  line-height: normal;
}

// Slick slider
.slick-slide {
  outline: none;
}
.slick-arrow {
  height: 70px;
  width: 70px;
  padding: 0;
  position: absolute;
  top: 125px;
  border: none;
  outline: none !important;
  cursor: pointer;
  background-color: $white-color;
  font-size: 0;
  z-index: 2;
  @include media-breakpoint-down(sm) {
    top: 170px;
  }
  &.slick-next {
    right: 200px;
    background: url(../img/slider-right.svg) no-repeat center;
    @include media-breakpoint-down(lg) {
      right: -35px;
    }
    @include media-breakpoint-down(sm) {
      right: -52px;
    }
  }
  &.slick-prev {
    left: 200px;
    background: url(../img/slider-left.svg) no-repeat center;
    @include media-breakpoint-down(lg) {
      left: -35px;
    }
    @include media-breakpoint-down(sm) {
      left: -52px;
    }
  }
}
.slick-list {
  min-width: 100%;
}
// Beer slider

.beer-handle {
  width: 43px;
  height: 43px;
  background: url(../img/handle.svg) no-repeat center !important;
  @include border-radius(0);
  -o-box-shadow: none !important;
  -webkit-box-shadow: none !important;
  -moz-box-shadow: none !important;
  box-shadow: none !important;
  &::after,
  &::before {
    border: none;
    background-color: $white-color;
    width: 3px;
    -ms-transform: rotate(0) !important;
    -o-transform: rotate(0) !important;
    -webkit-transform: rotate(0) !important;
    -moz-transform: rotate(0) !important;
    transform: rotate(0) !important;
    height: 300px;
  }
  &:before {
    left: 20px;
    top: -300px;
    height: 300px;
    bottom: auto;
  }
  &:after {
    left: 20px;
    top: auto;
    height: 300px;
    bottom: -300px;
  }
}

@include media-breakpoint-up(sm) {
  .desktop {
    display: block;
  }
  .mobile {
    display: none;
  }
}
@include media-breakpoint-down(xs) {
  .mobile {
    display: block;
  }
  .desktop {
    display: none;
  }
}

// Cookies
#cookie-notice {
  -ms-box-shadow: 0 2px 3px #172b3b;
  -o-box-shadow: 0 2px 3px #172b3b;
  -webkit-box-shadow: 0 2px 3px #172b3b;
  -moz-box-shadow: 0 2px 3px #172b3b;
  box-shadow: 0 2px 3px #172b3b;
  .cookie-notice-container {
    .btn {
      padding: 10px 22px;
    }
  }
  @include media-breakpoint-down(lg) {
    #cn-notice-text {
      display: block;
      margin-bottom: 10px;
    }
  }
}